.coordinator-block {
    position: relative;

    display: flex;
    max-width: pxToVw3(1000);

    padding-bottom: 50px;

    &::before {
        content: "";
        width: 1px;
        height: 100%;

        position: absolute;
        left: 50%;
        top: 0;

        transform: translateX(-50%);

        background-color: #65a2c2;
    }
    &__slider {
        position: relative;

        max-width: 50%;
        max-height: 900px;

        display: flex;
        flex-direction: column;
        .section {
            &__title2 {
                margin-top: 0;
            }
        }

        

        .footer {
            &__portfolio-back {
                position: absolute;
                bottom: pxToVw(-39);
                left: 0;
    
                z-index: 10;
            }
            
            &__portfolio-forward {
                position: absolute;
                bottom: pxToVw(-39);
                left: pxToVw3(130);
    
                z-index: 10;
            }
        }

        &:nth-child(2) {
            padding-right: 0;
            padding-left: 0;

            border-right: none;

            .coordinator-block {
                &__switch {
                    left: pxToVw3(65);
                }
            }

            .section {
                &__title2 {
                    padding-left: pxToVw3(60);
                    font-size: pxToVw3(30);
                }
            }

            .footer {
                &__portfolio-back {
                    left: pxToVw(20);
                }
            }
        }
    }

    &__item {
        padding-top: pxToVw3(25);
        padding-bottom: pxToVw3(25);
        padding-right: pxToVw3(50);
        padding-left: pxToVw3(45);

        text-align: center;
        p {
            font-size: pxToVw3(13);
            font-weight: 300;
            line-height: pxToVw3(15);

            margin-bottom: pxToVw3(40);
        }

        &:nth-child(odd) {
            border-right: 1px solid #D1E3ED;
        }

        .full-menu__form-submit {
            margin: 0 auto;

            height: auto;
        }
    }

    &__slider-wrapper {
        width: 100%;
        height: 100% !important;
        display: flex;
        transition: transform 1s ease-in-out;
    }

    &__switch {
        position: absolute;
        bottom: pxToVw(-40);
        font-size: pxToVw(12);
        display: flex;
        color: #67a1c4;
        left: pxToVw(40);
        user-select: none;
        transition: opacity 1s ease;

        .current {
            font-weight: 700;
        }
    }

    &__item-img {
        margin-bottom: pxToVw3(45);

        z-index: 10;
        position: relative;

        height: pxToVw3(170);

        background-repeat: no-repeat;
        background-size: cover;
        background-position: center center;
        img {
            width: 100%;
            height: 100%;
            z-index: 10;
            position: relative;
        }

        &::after {
            content: '';
            display: block;
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            background: url(../img/coordinator-block-mask.svg);
            background-size: 100% auto;
            background-position: top center;
            z-index: 50;
        }
    }

    &__item-title {
        font-size: pxToVw3(18);
        font-weight: 700;

        margin-bottom: pxToVw3(8);
    }

    &--delivery {
        max-width: none;
        width: 100%;
        margin-left: 20px;

        padding-bottom: 50px;

        
        .section {
            &__title2 {
                font-family: "Charter", serif;
                font-size: pxToVw(22);
                margin-bottom: pxToVw(14);
                width: 100%;
            }
        }

        .coordinator-block {
            &__slider {
                &:nth-child(2) {
                    .section__title2 {
                        font-size: pxToVw(22);
                    }
                }
            }
        }

        .coordinator-block__item {
            padding-bottom: 0;
            p {
                font-size: pxToVw(12);
                line-height: pxToVw(16);

                margin-bottom: 25px;
            }
        }

        .coordinator-block__item-img {
            height: pxToVw3(315);
        }
    }
}

@media (min-width: 1600px) {
    .coordinator-block {
        &--delivery {
            margin-left: 0;
        }
    }
}

@media (max-width: 1024px) {
    .coordinator-block {
        max-width: none;
        flex-direction: column;

        padding-bottom: 80px;
        &__slider {
            max-width: none;
            margin-right: 0;
            padding-bottom: 50px;

            height: auto;
            border: none;

            max-height: none;

            .footer {
                &__portfolio-back {
                    bottom: -5px;
                    left: 0;
                    img {
                        width: 16px;
                    }
                }
                
                &__portfolio-forward {
                    bottom: -5px;
                    left: 70px;
                    img {
                        width: 16px;
                    }
                }
            }

            &:nth-child(2) {
                .footer {
                    &__portfolio-back {
                        bottom: -5px;
                        left: 0;
                        img {
                            width: 16px;
                        }
                    }
                }

                .section {
                    &__title2 {
                        padding-left: 0;
                        font-size: 20px;
                    }
                }

                .coordinator-block {
                    &__switch {
                        bottom: 42px;
                        left: 25px;
                    }
                }
            }
        }

        &__item-img {
            max-width: 150px;
            height: 160px;

            margin: 0 auto;
            margin-bottom: 15px;
        }

        &__item-title {
            font-size: 14px;
        }

        &__item {
            padding-top: 10px;
            padding-right: 10px;
            padding-left: 10px;
            padding-bottom: 30px;
            &:nth-child(2) {
                padding-left: 0;
            }

            p {
                font-size: 14px;
                line-height: 16px;
                max-width: 200px;

                margin: 0 auto;
                margin-bottom: 10px;
            }

            .full-menu__form-submit {
                width: 150px;
                font-size: 11px;
            }
        }

        &__switch {
            bottom: 42px;
            left: 25px;
        }

        &__slider-wrapper {
            width: auto;
            height: auto;
            display: block;
        }

        .section__title2 {
            font-size: 25px;
            line-height: 28px;

            text-align: center;
        }

        &::before {
            display: none;
        }
    }
}

@media (max-width: 400px) {
    .coordinator-block {
        &__item {
            padding-left: 20px;
            padding-right: 20px;

            p {
                font-size: 12px;
            }

            .full-menu__form-submit {
                width: 100%;
            }

            &:nth-child(2) {
                padding-left: 15px;
            }

            &:nth-child(2) {
                border: none;
            }
        }

        

        &__slider {
            padding-bottom: 40px;

            
            .footer {
                &__portfolio-back {
                    bottom: 4px;

                    left: 50%;
                    transform: translateX(-50px);
                }
                
                &__portfolio-forward {
                    bottom: 4px;
                    left: 50%;
                    transform: translateX(-30px);
                }
            }

            &:nth-child(2) {
                .footer {
                    &__portfolio-back {
                        bottom: 4px;
                        left: 50%;
                        transform: translateX(-50px);
                    }
                }

                .coordinator-block__switch {
                    left: 50%;
                    transform: translateX(-50%);
                }
            }
        }

        &__switch {
            left: 50%;
            transform: translateX(-50%);
        }

        // &__slider-wrapper {
        //     max-height: 400px;
        // }
    }
}


